import {  
	mapState,
    mapMutations  
} from 'vuex';
export default {
	data() {
		return {
			
		};
	},
	computed:{
		...mapState(['hasLogin'])
	},
	methods:{
		...mapMutations(['logout']),

		navTo(url){
			if(!this.hasLogin){
				url = '/pages/public/login';
			}
			uni.navigateTo({  
				url
			})  
		}, 
		clearCache(){
			uni.showLoading({
				title:'加载中'
			});
			setTimeout(()=>{
				uni.clearStorage();
				uni.hideLoading();
				this.$api.msg('清除成功');
			},2000);
		},
		//退出登录
		toLogout(){
			uni.showModal({
			    content: '确定要退出登录么',
			    success: (e)=>{
			    	if(e.confirm){
			    		this.logout();
			    		setTimeout(()=>{
			    			uni.navigateBack();
			    		}, 200)
			    	}
			    }
			});
		},
		//switch
		switchChange(e){
			let statusTip = e.detail.value ? '打开': '关闭';
			this.$api.msg(`${statusTip}消息推送`);
		},

	}
}
